.app {
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  padding: 20px;
}

.topic-selection {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
  text-align: center;
}

.topic-selection h1 {
  color: #2c3e50;
  margin-bottom: 40px;
  font-size: 2.5rem;
}

.topics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  padding: 20px;
}

.topic-button {
  background: white;
  border: none;
  border-radius: 12px;
  padding: 20px;
  font-size: 1.2rem;
  color: #2c3e50;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.topic-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.results {
  max-width: 600px;
  margin: 0 auto;
  padding: 40px 20px;
  text-align: center;
}

.results h1 {
  color: #2c3e50;
  margin-bottom: 30px;
  font-size: 2.5rem;
}

.results-card {
  background: white;
  border-radius: 15px;
  padding: 40px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.results-card h2 {
  color: #2c3e50;
  margin-bottom: 20px;
  font-size: 1.8rem;
}

.score {
  font-size: 3rem;
  color: #2c3e50;
  margin-bottom: 10px;
}

.score-number {
  font-weight: bold;
}

.score-total {
  color: #7f8c8d;
  font-size: 2rem;
}

.percentage {
  font-size: 2.5rem;
  color: #27ae60;
  margin-bottom: 30px;
}

.restart-button {
  background: #3498db;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 15px 30px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background 0.3s ease;
}

.restart-button:hover {
  background: #2980b9;
} 