.quiz-results {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: white;
  border-radius: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.results-summary {
  text-align: center;
  margin-bottom: 30px;
}

.results-summary h2 {
  color: #333;
  margin-bottom: 20px;
}

.summary-stats {
  display: flex;
  justify-content: space-around;
  margin: 20px 0;
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 10px;
  min-width: 150px;
}

.stat-label {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 5px;
}

.stat-value {
  color: #333;
  font-size: 1.5rem;
  font-weight: bold;
}

.results-chart {
  margin: 30px 0;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 10px;
}

.question-details {
  margin-top: 30px;
}

.question-details h3 {
  color: #333;
  margin-bottom: 20px;
}

.details-table {
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  overflow: hidden;
}

.table-header {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  background: #f8f9fa;
  padding: 15px;
  font-weight: bold;
  color: #333;
  border-bottom: 2px solid #e0e0e0;
}

.table-row {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  padding: 12px 15px;
  border-bottom: 1px solid #e0e0e0;
  align-items: center;
}

.table-row:last-child {
  border-bottom: none;
}

.positive {
  color: #28a745;
  font-weight: 500;
}

.negative {
  color: #dc3545;
  font-weight: 500;
}

.correct {
  color: #28a745;
  font-weight: 500;
}

.incorrect {
  color: #dc3545;
  font-weight: 500;
} 