.quiz-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.quiz-progress {
  text-align: left;
  color: #333;
  font-size: 1.5rem;
  font-weight: 500;
}

.quiz-score {
  text-align: right;
  color: #333;
  font-size: 1.5rem;
  font-weight: 500;
}

.timer-container {
  width: 100%;
  height: 4px;
  background-color: #e0e0e0;
  border-radius: 2px;
  margin-bottom: 20px;
  overflow: hidden;
}

.timer-progress {
  height: 100%;
  background-color: #3498db;
  transition: width 0.1s linear, background-color 0.3s ease;
}

.timer-progress.timer-warning {
  background-color: #e74c3c;
  animation: pulse 1s infinite;
}

.question-card {
  background: white;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.question-card h2 {
  color: #333;
  margin-bottom: 30px;
  font-size: 1.5rem;
  line-height: 1.4;
}

.options-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.option-button {
  background: white;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  padding: 15px 20px;
  font-size: 1rem;
  color: #333;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  display: flex;
  align-items: center;
}

.option-button:hover:not(:disabled) {
  border-color: #007bff;
  background-color: #f8f9fa;
}

.option-button.correct {
  background-color: #d4edda;
  border-color: #28a745;
  color: #155724;
}

.option-button.incorrect {
  background-color: #f8d7da;
  border-color: #dc3545;
  color: #721c24;
}

.option-button.correct-answer {
  background-color: #d4edda;
  border-color: #28a745;
  color: #155724;
  animation: pulse 1s ease-in-out;
}

.option-button:disabled {
  cursor: default;
  opacity: 0.8;
}

.option-prefix {
  font-weight: bold;
  margin-right: 10px;
  min-width: 20px;
}

@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

.quiz-navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  gap: 12px;
}

.nav-button {
  background: #3498db;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 28px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s;
  margin: 0 0 8px 0;
}

.nav-button:disabled {
  background: #b2bec3;
  cursor: not-allowed;
}

.nav-button.finish {
  background: #27ae60;
}

.nav-button.finish:hover {
  background: #219150;
}

.nav-button:hover:not(:disabled) {
  background: #2980b9;
  transform: translateY(-2px);
}

.quiz-reels-container {
  height: 100vh;
  overflow-y: auto;
  scroll-snap-type: y mandatory;
  background-color: #f5f5f5;
}

.quiz-reels-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
}

.quiz-reel {
  min-height: 100vh;
  width: 100%;
  max-width: 600px;
  padding: 20px;
  scroll-snap-align: start;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.quiz-finish-bar {
  position: sticky;
  bottom: 0;
  width: 100%;
  background: rgba(255,255,255,0.95);
  display: flex;
  justify-content: center;
  padding: 20px 0 10px 0;
  z-index: 10;
}

.quiz-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
} 